import React, {Component} from 'react';
import YouTube from 'react-youtube';
import './Videos.scss';
import ScrollHelper from '../ScrollHelper/ScrollHelper';

export default class Videos extends Component {
  constructor(props) {
    super(props);

    this.state = {
      opts: {
        height: window.innerWidth <= 600? window.innerWidth / (16 / 9) : 340,
        width: window.innerWidth <= 600 ? window.innerWidth : 600,
        playerVars: { // https://developers.google.com/youtube/player_parameters
          autoplay: 0
        }
      }
    }
  }
  // componentDidMount () {
  //   console.log("Mount");
  //   // console.log(screen.orientation.angle);
  //   window.addEventListener('resize', () => {
  //     console.log("change");
  //     this.forceUpdate();
  //     this.setState({
  //       opts: {
  //         height: window.innerWidth <= 600? window.innerWidth / (16 / 9) : 340,
  //         width: window.innerWidth <= 600 ? window.innerWidth : 600,
          
  //         playerVars: { // https://developers.google.com/youtube/player_parameters
  //           autoplay: 0
  //         }
  //       }
  //     })
  //   });
    
  // }
  _onReady(event) {
    // access to player in all event handlers via event.target
    event.target.pauseVideo();
  }
  render () {
    return (
      <>
      <div className="container">
        <div className="page-videos">
          <div className="video">
            <YouTube
              videoId="Gw6OVi96oz0"
              opts={this.state.opts}
              onReady={this._onReady}
            />
          </div>
          <div className="video"> 
            <YouTube
              videoId="clRlY-sbz3M"
              opts={this.state.opts}
              onReady={this._onReady}
            />
          </div>
        <ScrollHelper className="page-gallery-scroll" height="100px"/>
        </div>
        {/* <div className="page-videos-button">
          <a href="https://www.youtube.com/user/treblerspl" target="_blank" rel="noopener noreferrer">WIĘCEJ FILMÓW &gt;&gt;</a>
        </div> */}
      </div>
      </>
    );
  }
}
