import React from 'react';
import './MenuItem.scss';
import ScrollIntoView from 'react-scroll-into-view';

export default class MenuItem extends React.Component {
  
  animate = () => {
    
    if (!this.props.clickSelector) return;
    const itemToAnimate = document.querySelector(this.props.clickSelector);
    itemToAnimate.classList.add("darkened-background");
    setTimeout(() => {
      itemToAnimate.classList.remove('darkened-background');
    }, 200);
  }

  render() {
    const scroller = this.props.selector ? 
    (
      <ScrollIntoView 
        selector={this.props.selector}
        alignToTop={true}
        className="scroll-button"
      >
        {this.props.contents}
      </ScrollIntoView>
    ) :
    (<div className="scroll-button">{this.props.children}{this.props.contents}</div>);
    return (    
      <li className="list-item"
        onClick={() => {
          this.animate();
          // this.props.toggleHamburger();
          if (this.props.clickAction) this.props.clickAction();
        }}
      >
      {scroller}
        
      </li>
    );
  }
}
