import React from 'react';
import './Menu.scss';
import MenuItem from './MenuItem/MenuItem';
import Shows from '../../Info/Shows/Shows';

export default class Menu extends React.Component {

  constructor () {
    super();
    this.state = {
      isMenuVisible: false,
      areShowsOn: false
    }
  }

  toggleMenu = () => {
    this.setState({
      isMenuVisible: !this.state.isMenuVisible
    });
  }

  toggleShows = () => {
    this.setState({
      areShowsOn: !this.state.areShowsOn
    })
  }

  componentDidMount () {
    document.addEventListener('touchend', (e) => {
      // if (!e.target.classList.contains('list-item') || !e.target.classList.contains('scroll-button')) {
        this.setState({isMenuVisible: false})
      // }
    })
  }

  render() {
    
    return (
      <nav className={`menu ${this.state.isMenuVisible ? "menu-show" : ""}`}>    
          <div className="hamburger-icon" onClick={this.toggleMenu}>
            <div className="hamburger-line line-top"></div>
            <div className="hamburger-line line-middle"></div>
            <div className="hamburger-line line-bottom"></div>
          </div>
          <ul>
              <div className="small-screen-gap"></div>
                <MenuItem 
                  contents="O NAS" 
                  selector = ".page-info-card-scroll" 
                  clickSelector = '.page-info'
                  toggleHamburger = {this.toggleMenu}
                />
              <MenuItem 
                contents="SHOW" 
                selector = ".page-info-card-scroll"
                clickSelector = '.feel-the-reel'  
                toggleHamburger = {this.toggleMenu}
              />
              <MenuItem 
                contents="OFERTA" 
                selector = ".page-info-card-scroll"
                clickSelector = '.offer'
                toggleHamburger = {this.toggleMenu}
              />
              <MenuItem contents="FILMY" selector=".page-videos-scroll"  toggleHamburger = {this.toggleMenu}/>
              <MenuItem contents="GALERIA" selector=".page-gallery-scroll"  toggleHamburger = {this.toggleMenu}/>
              <MenuItem 
                contents="KONCERTY"
                toggleHamburger = {this.toggleMenu}
                clickAction = {this.toggleShows}
              >
               
                <Shows clickAction = {this.toggleShows} isShowsOn={this.state.areShowsOn}/>
                
              </MenuItem>
              <MenuItem contents="KONTAKT" selector=".page-footer-scroll"  toggleHamburger = {this.toggleMenu}/>
          </ul>        
      </nav>
    )
  }
}
